<template>
    <div class="project-list">
        <!-- search -->
        <div class="mb10">
            <el-input v-model="query.project_name" placeholder="项目／客户名称" class="w150"></el-input>
            <el-select v-model="query.industry_type_id" class="w150" placeholder="选择行业">
                <el-option v-for="(item,index) in industryType" :key="index" :label="item.label" :value="item.value"></el-option>
            </el-select>
            <el-select v-model="query.progress" class="w150" placeholder="选择项目进展">
                <el-option v-for="(item,index) in industryType" :key="index" :label="item.label" :value="item.value"></el-option>
            </el-select>
            <span>负责人</span>
            <el-input v-model="query.name" placeholder="人员姓名" class="w150"></el-input>
            <el-date-picker 
                v-model="dateRange" 
                type="daterange" 
                align="right" 
                placeholder="选择日期范围" 
                class="w240" 
                range-separator="-" 
                :clearable="false" 
                @change="onRangeChange">
            </el-date-picker>
            <el-button type="primary" @click="onSearch">查询</el-button>
            <el-button @click="onReset">清空</el-button>
        </div>

        <div class="mb10">
            <el-button type="primary" @click="addProject">新建项目</el-button>
            <el-button type="primary" @click="editDirector">修改负责人</el-button>
            <el-button @click="exportExcel">导出</el-button>
        </div>

        <!-- table list -->
        <el-table :data="tableParams.list" header-align="center" border header-row-class-name="header-center">
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column label="编号" prop="project_no"></el-table-column>
            <el-table-column label="项目名称" prop="project_name">
                <template slot-scope="scope">
                    <span class="text-link" @click="toDetail(scope.row.id)">{{scope.row.name}}</span>
                </template>
            </el-table-column>
            <el-table-column label="客户名称" prop="customer_name"></el-table-column>
            <el-table-column label="联系人" prop="stakeholder_name"></el-table-column>
            <el-table-column label="联系方式" prop="stakeholder_contact"></el-table-column>
            <el-table-column label="项目进展" prop="progress_name"></el-table-column>
            <el-table-column label="创建时间" prop="create_time"></el-table-column>
            <el-table-column label="客户级别" prop="customer_level">
                <template slot-scope="scope">
                    <span>{{customerLevelText[scope.row.customer_level]}}</span>
                </template>
            </el-table-column>
            <el-table-column label="负责人" prop="director_name"></el-table-column>
            <el-table-column label="操作">
                <template slot-scope="scope">
                    <el-button type="text" size="mini" @click="doEdit(scope.row)">编辑</el-button>
                    <el-button type="text" size="mini" @click="doDelete(scope.row)">删除</el-button>
                </template>
            </el-table-column>

        </el-table>
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="tableParams.currentPage" :page-sizes="tableParams.pageSizeList" :page-size="tableParams.pageSize" :layout="tableParams.layout" :total="tableParams.totalCount" v-if="tableParams.totalCount" class="mt15">
        </el-pagination>


        <edit-modal v-model="modalShow" :project="editProject" @on-save="handlerEditModalSave"></edit-modal>
    </div>
</template>
<script>
import tableMixins from "@/mixins/tableMixin";
import editModal from './edit-modal.vue';
import * as constants from './constants.js';
import { customerLevel } from '@/views/const.js';

export default {
    data() {
        return {
            industryType: [],
            dateRange: '',
            modalShow: false,
            query: constants.initProjectListQuery(),
            editProject: constants.initProject(),
            customerLevelText: {
                '1': 'VIP',
                '2': '重要客户',
                '3': '普通客户',
            },
        };
    },
    mixins: [tableMixins],
    
    components: {editModal},
    methods: {
        _loadList() {
            let params = this.assignQuery(this.query)
            // todo
            this.tableParams.list = constants.getTestListData()
            return this.tableParams.total = this.tableParams.list.length

            this.$api.project.getProjectList(params).then(data => {
                this.tableParams.list = data.rows
                this.tableParams.total = data.total
            })

        },
        onRangeChange(timeRange){
            this.query.create_time = `${timeRange[0]} - ${timeRange[0]}`
        },
        onReset(){},
        addProject(){
            this.editProject = constants.initProject()
            this.modalShow = true
        },
        doEdit(row){
            this.editProject = constants.initProject()
            this.$api.project.getProjectDetail(row.id).then(data => {
                this.editProject = data
            })
            
            this.modalShow = true
        },

        editDirector(){
            console.log('editDirector')
        },
        exportExcel(){
            console.log('exportExcel')
        },
        toDetail(id){
            this.$router.push({name: 'projectDetail', params:{projectId: id}})
        },
        doDelete(row){
            console.log('doDelete row')
        },
        handlerEditModalSave(){
            this._loadList()
        }
    },
    created() {},
};
</script>


